.app {
  position: fixed;
  top: 0px;
  bottom: 0px;
  left: 0px;
  right: 0px;
  overflow: hidden;
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #4c4c4c;
}
a {
  color: #4c4c4c;
}
.fade-enter,
.fade-leave-to {
  opacity: 0;
}
.fade-enter-active,
.fade-leave-active {
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}
.slide-left-enter,
.slide-right-leave-active {
  transform: translateX(100%);
  -webkit-transform: translateX(100%);
  opacity: 0;
}
.slide-left-leave-active,
.slide-right-enter {
  opacity: 0;
  transform: translateX(-30px);
  -webkit-transform: translateX(-30px);
}
.slide-left-enter-active,
.slide-left-leave-active {
  -webkit-transition: all 0.3s ease;
  transition: all 0.3s ease;
}
.slide-up-enter,
.slide-up-leave-active {
  -webkit-transform: translateY(100%);
          transform: translateY(100%);
  opacity: 0;
}
.slide-up-enter-active,
.slide-up-leave-active {
  -webkit-transition: all 0.3s;
  transition: all 0.3s;
}
.bx-list {
  padding-bottom: 0.25rem;
  padding-top: 0.25rem;
  border-bottom: 1px solid #EFEFEF;
  display: block;
}
.bx-list > img {
  width: 30%;
}
.bx-list .bx-list-right {
  width: 65%;
  float: right;
}
.bx-list .bx-list-right h3 {
  color: #000;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}
.bx-list .bx-list-right p {
  color: #666;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  font-size: 12px;
  margin-top: 0.15rem;
}
.bx-list .bx-list-right span {
  display: inline-block;
  margin-top: 0.2rem;
  padding: 2px 5px;
  border: 1px solid #448EF6;
  color: #448EF6;
  border-radius: 3px;
  float: right;
  font-size: 12px;
}
.bxnews {
  border-top: 0.2rem solid #EFEFEF;
}
.bxnews .bx-list-right p {
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 3;
  overflow: hidden;
  white-space: normal;
}
.bxnews .bx-list:last-child {
  border-bottom: 0px;
}
