/* 重置默认字体和大小 */
body{ font-size:0.28rem; font-family:Microsoft Yahei,Helvetica,Arial,sans-serif; width:7.5rem;margin:0px auto;/* color:#606366; background:#f2f2f2; */}
h1,h2,h3,h4,h5,h6,b{font-size:100%;font-weight:normal}
em{font-style:normal}
strong{font-weight:700}
button,input,select,textarea{font-size:100%;-moz-appearance:none; -ms-progress-appearance:none; -webkit-appearance:none;font-family:Helvetica,Arial,sans-serif;}
/*重置文本格式*/
a{text-decoration:none}
/*;  color:#525252; */
q:before,q:after{content:""}
/* 重置表格元素 */
table{border-collapse:collapse;border-spacing:0}
/* 去除默认边框 */
fieldset,img{border:none}
/* 重置列表元素 */
ul,ol{list-style:none}
/* 媒体元素 */
audio:not([controls]){display:none;height:0}
.display-flex,.wap-app > ul{display:-webkit-box;display:-moz-flex;display:-ms-flexbox;display:-ms-flex;display:flex}
.flex-box,.search-page .search-input{display:-webkit-box;display:-moz-flex;display:-ms-flexbox;display:-ms-flex;display:flex}
/* float */
.fd-left{float:left}
.fd-right{float:right}
.fd-clear{clear:both}
.fd-clr:after{display:block;clear:both;height:0;content:" "}
.clearfix:before,.clearfix:after{content:" ";display:table;}
.clearfix:after{clear:both;}
/* position */
.fd-locate{position:relative}
.fd-locate .fd-locate-center{
  position:absolute;
  left:50%;
  top:50%;
  transform:translate(50%,-50%);
  -webkit-transform:translate(-50%,-50%);
  -o-transform:translate(-50%,-50%);
  -ms-transform:translate(-50%,-50%);
  -moz-transform:translate(-50%,-50%);
}
.fd-locate .fd-locate-centerY{
  position:absolute;
  top:50%;
  transform:translateY(-50%);
  -webkit-transform:translateY(-50%);
  -moz-transform:translateY(-50%);
  -ms-transform:translateY(-50%);
  -o-transform:translateY(-50%);
}
.fd-locate .fd-locate-centerX{
  position:absolute;
  left:50%;
  transform:translateX(-50%);
  -webkit-transform:translateX(-50%);
  -moz-transform:translateX(-50%);
  -ms-transform:translateX(-50%);
  -o-transform:translateX(-50%);
}
.fd-backdrop{
  position:fixed;
  top:0px;
  bottom:0px;
  left:0px;
  right:0px;
  background:rgba(0,0,0,0.5);
}
/* display */
.fd-hide{display:none}
.fd-show{display:block}
.dib{display: inline-block;}
.fd-table{ display:table; width:100%;}
/* 设置元素的盒模型为flex */
.flex{display:-webkit-box;display:-moz-flex;display:-ms-flexbox;display:-ms-flex;display:flex}
.flex_align{-webkit-box-align:center;-ms-flex-align:center;align-items:center;}
.flex_justify{-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;}
.flex .flex_div{-webkit-box-flex:1;-ms-flex:1 1 0px;flex:1 1 0; }
/* 禁止长按链接与图片弹出菜单 */
