/* 设置元素的盒模型为border-box */
*:before,*:after{-webkit-box-sizing:border-box;box-sizing:border-box;-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;-moz-text-size-adjust:100%;text-size-adjust:100%;-webkit-tap-highlight-color:rgba(0,0,0,0)}
/*去除元素周围的虚线*/
*:focus{outline:none}
*{
   box-sizing:border-box;
  -webkit-box-sizing:border-box;
  -moz-box-sizing:border-box;
  -ms-box-sizing:border-box;
  -o-box-sizing:border-box;
  -webkit-tap-highlight-color: transparent;

}
/***input输入框***/
input:-moz-placeholder {
  color: #b2b2b2;
}
input::-moz-placeholder {
  color: #b2b2b2;
}
input:-ms-input-placeholder {
  color: #b2b2b2;
}
input::-webkit-input-placeholder {
  color: #b2b2b2;
}
/*模拟checkbox 选中效果:红色背景 圆点*/
.check-tip {
  display: inline-block;
  height: 0.26rem;
  width: 0.26rem;
  border: 1px solid #EEEEEE;
  border-radius: 50%;
  margin-left: 0.26rem;
  vertical-align: middle;
  margin-right: 0.1rem;
}
.check-tip.checked {
  background: #FE2B53;
  -webkit-box-shadow: 0px 0px 2px #fc5270;
          box-shadow: 0px 0px 2px #fc5270;
}
/**表单**/
.form-group{
  display:-webkit-box;
  display:-moz-flex;
  display:-ms-flexbox;
  display:-ms-flex;
  display:flex;
  height:0.8rem;
  line-height:0.8rem;
  margin-bottom:0.15rem;
}
.form-group-label{
  width:2rem;
  text-align:center;
}
.form-input-block{
  display:block;
  width:100%;
}
.form-control {
  display: block;
  width: 100%;
  height: 100%;
  padding: 6px 12px;
  font-size: 14px;
  line-height: 1.42857143;
  color: #555;
  background-color: #fff;
  outline:none;
  background-image: none;
  border: 1px solid #ccc;
  border-radius: 4px;
  -webkit-box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
          box-shadow: inset 0 1px 1px rgba(0, 0, 0, .075);
  -webkit-transition: border-color ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
          transition: border-color ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
          transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s;
          transition: border-color ease-in-out .15s, box-shadow ease-in-out .15s, -webkit-box-shadow ease-in-out .15s;
}
.form-group-radio  input[type="radio"],.form-group-checkbox  input[type="checkbox"]{
   width:0.36rem;
   height:0.36rem;
   border-radius:50%;
   border:1px solid #ddd;
   margin-right:0.15rem;
   vertical-align:middle;
   margin-top:-0.1rem;
}
.form-group-radio .radio label,.form-group-checkbox .checkbox label{margin-right:0.3rem;}
.form-group-radio  input[type="radio"]:checked,.form-group-checkbox  input[type="checkbox"]:checked{
  background:red;
  border:1px solid red;
}
/**面板**/
.panel {
  margin-bottom: 20px;
  background-color: #fff;
  border: 1px solid transparent;
  border-radius: 4px;
  border:1px solid rgba(0, 0, 0, .05);
}
.panel-header {
  padding: 10px 15px;
  border-bottom: 1px solid transparent;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  -webkit-box-shadow: 0 1px 1px rgba(0, 0, 0, .05);
          box-shadow: 0 1px 1px rgba(0, 0, 0, .05);
}
.panel-body,.panel-footer {
  padding: 15px;
}
.panel-footer{
  -webkit-box-shadow: 0 -1px 1px rgba(0, 0, 0, .05);
          box-shadow: 0 -1px 1px rgba(0, 0, 0, .05);
}
/**左右布局**/
.box-list{
  display:-webkit-box;
  display:-moz-flex;
  display:-ms-flexbox;
  display:-ms-flex;
  display:flex;
}
.box-list-media{
  width:25%;
  text-align:center;
}
.box-list-media img{width:80%;}
.box-list-content{
  -webkit-box-flex:1;
      -ms-flex:1;
          flex:1;
  -webkit-flex:1;
}
